*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
}

body {
position: relative;
min-width: 320px;
max-width: 1200px;
display: flex;
flex-direction: column;
min-height: 100vh;
margin: 0 auto;
font-style: normal;
font-family: "PT Sans", sans-serif;
font-size: 14px;
font-weight: 400;
color: #000000;
background-color: #FFFFFF;
box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}
